import config from './config.json';
import { Cookies } from 'react-cookie';


const cookies = new Cookies();
// For axios
export const apiURL = process.env.NODE_ENV === 'production' ? cookies.get("api_url_prod") : config.LOCAL_API_URL;
const axios = require("axios");

const axiosInstance = axios.create({
    baseURL: apiURL,
    // TODO: Change Auth Token dyanamic
    // headers : cookies.get('header')
    headers: {"Authorization": cookies.get('access_token') ? "Token "+cookies.get('access_token') : ""}
});

export function graphQL(data) {
    return axiosInstance.post("/graphql", data);
}

export function getRecommendation(data) {
    return axiosInstance.get("/items", data);
}
