import React from 'react';
import "./MoreOption.scss";

const MoreOption = (props) => {
	const ref = React.createRef();

	const showDropdown = () => {
		ref.current.classList.toggle("show");
	}

	return (
		<div className="more-option">
			<button className="more-option-dropbtn icon-more-hori my-auto" onClick={showDropdown}></button>
			<div className={["more-option-dropdown"].join(" ")} ref={ref}>
				<div className="more-option-content">
					<ul className="p-0 m-0">
						{props.data.map((option, index) =>
							<li className="py-1 px-1" key={"option-" + index}><button className="more-option-item" onClick={() => { showDropdown(); option.action(); }}>{option.name}</button></li>
						)}
					</ul>
				</div>
			</div>
		</div>
	);
}

export default MoreOption;