import { gql } from '@apollo/client';

// Add queries

const GET_USERS = gql`
query getUsers {
    users {
        id,
        email
    }
}
`;

const GET_CATEGORIES = `
query getCategories($registryId: Int!){
  categories(registryId:$registryId){
    id,
    categoryName,
    isRoot
    subCategories{
      id,
      categoryName,
      addedItemsCount,
      totalItemsCount
    }
    addedItemsCount,
    totalItemsCount
  }
 }
`;

const GET_PRODUCTS = `
query registryItems($registryId:Int!){
    registryItems(registryId:$registryId){
      edges{
        node{
          id,
          isGroupGift,
          isReserved,
          quantity,
          mostWanted,
          orderNo,
          giftMessage,
          isReserved,
          isAmountReceived,
          isPurchased,
          item{
            id,
            title,
            shopifyItemId,
            note,
            sku,
            price,
            imageUrl,
            category {
              id,
              categoryName,
              isRoot,

            }
          }
        }
      }
    }
  }
  
`

const GET_RECOMMENDATIONS = `
query recommendation($productCount: Int!, $productType: String!){
  products(first:$productCount, 
    query:$productType){
      edges{
        node{
          id,
          handle, 
          title, 
          description, 
          vendor,
          onlineStoreUrl,  
          variants (first:1){
            edges {
              node {
                price,
                sku,
                image{
                  originalSrc
                }
              }
            }
          }
        }
      }
    }
  }
`;

const ADD_REGISTRY_ITEM =`
mutation addRegistryItems(
    $registryId:Int!, 
    $title:String!, 
    $category:Int!, 
    $tags:[Int]!, 
    $storeItemUrl:String!, 
    $sku:String!,
    $price:Float!,
    $quantity:Int!,
    $shopifyItemId:String,
    $imageUrl: String){
  addRegistryItems(registryId:$registryId,
  registryItems:[{
    storeName:"amazon",
    items: [{
      title:$title,
      category:$category,
      tags:$tags,
      storeItemUrl:$storeItemUrl,
      sku:$sku,
      price:$price,
      quantity:$quantity,
      shopifyItemId: $shopifyItemId,
      imageUrl: $imageUrl
    },]
  }])
  {
    message
  }
}
`;

const UPDATE_REGISTRY_ITEM = `
mutation updateRegistryItems($registryItemId: Int!, $quantity: Int!){
  updateRegistryItems(registryItemId:$registryItemId, registryItemData:{
    quantity:$quantity,
    isReserved:false,
  }){
    message
  }
}
`;

const REMOVE_REGISTRY_ITEM = `
mutation deleteRegistryItems($registryItemId: Int!){
  deleteRegistryItems(registryItemId:$registryItemId){
    message
  }
}
`;

export {
  GET_USERS, 
  GET_CATEGORIES, 
  GET_PRODUCTS, 
  GET_RECOMMENDATIONS, 
  ADD_REGISTRY_ITEM,
  UPDATE_REGISTRY_ITEM,
  REMOVE_REGISTRY_ITEM
};

