import React from 'react';
import "./CardItem.scss";
import Counter from "modules/commons/components/counter/Counter";

const CardItem = (props) => {

	return (
		<div className={["card-item", props.className ? props.className : ""].join(" ")}>
			<div className="product-image">
				<a href={props.data.productLink}>
					<img src={props.data.variants.edges[0].node.image.originalSrc} alt={props.data.title} />
				</a>
			</div>
			<div className="product-bottom mt-3">
				<div className="product-title">
					<h5><a href={props.data.productLink}>{props.data.title}</a></h5>
				</div>

				<div className="product-sub-text-heading mt-3">
					<span>{props.data.subTextHeading}</span>
				</div>

				<div className="product-sub-text-description text-sm">
					<span>{props.data.description} <a href={props.data.onlineStoreUrl}>Read more</a></span>
				</div>

				<div className="product-price d-flex mt-3">
					<h5>${props.data.variants.edges[0].node.price}</h5>
					{props.data.strikedPrice ? <span className="strike-price ml-1">{props.data.strikedPrice}</span> : null}
				</div>
				<div className="d-flex align-items-center mt-3">
					<Counter
						counter={props.data.quantity}
						incrementHandler={props.incrementQuantityHandler}
						decrementHandler={props.decrementQuantityHandler} />

					<button className="add-btn ml-auto" onClick={props.addProductHandler}></button>
				</div>
			</div>
		</div>
	)
}

export default CardItem;