import React from 'react'
import "./AddedCard.scss";
import MoreOption from "../more-option/MoreOption";

const AddedCard = (props) => {

  const moreOptionsData = [
    {
      name: "View Item",
      action: () => alert("Clicked")
    },
    {
      name: "Mark as purchased",
      action: () => alert("Clicked")
    },
    {
      name: "View Recommendations",
      action: () => alert("Clicked")
    },
    {
      name: "Remove",
      action: props.removeProductFromCartHandler
    }
  ]
  return (
    <div className="added-card p-4 my-4">
      <div className="row">
        <div className="col-2">
          <a href={props.data.productLink}>
            <img className="product-img" src={props.data.item.imageUrl} alt={props.data.item.title}/>
          </a>
        </div>
        <div className="col">
          <div className="text-sm category">{props.data.item.category.categoryName}</div>
          <a href={props.data.productLink}>
            <span className="font-medium">{props.data.item.title}</span>
          </a>
          <div>${props.data.item.price}</div>
        </div>
        <div className="col-2 ml-auto">
          <MoreOption data={moreOptionsData}/>
          <button
            className={[
              "icon",
              "ml-4",
              props.data.favorite ? "heart-fill" : "heart",
            ].join(" ")}
            onClick={props.toggleFavorite}
          ></button>
        </div>
      </div>
    </div>
  )
}

export default AddedCard