import React from 'react';
import "./FilterItemChip.scss";

const FilterItemChip = (props) => {
  return (
    <span className={["filter-item-chip px-2 py-1 text-body mb-1", props.className ? props.className : ''].join(" ")}>{props.text}<i className="icon-close ml-1" onClick={props.deleteHandler}></i></span>
  )
}

export default FilterItemChip;