import React, { Component } from 'react';
import { incrementQuantity, decrementQuantity, addProductToCart, addedProducts, setRegistryProducts, setCheckListItems, addRecommendedProduct } from "../../../my-registry/MyRegistryActions";
import CardItem from "modules/commons/components/card-item/CardItem";
import { connect } from "react-redux";
import "./ViewRecommend.scss";
import MoreOption from "../more-option/MoreOption";
import { ADD_REGISTRY_ITEM, GET_PRODUCTS, GET_CATEGORIES, UPDATE_REGISTRY_ITEM } from 'api/queries';
import { graphQL, getRecommendation } from 'api/axios';
import ReducerDefaultData from 'data/ReducerDefaultData.json';

class ViewRecommend extends Component {

    getProducts = () => {
        let data = {
            query: GET_PRODUCTS,
            variables: {
                registryId: parseInt(ReducerDefaultData.myRegistry.registryId)
            }
        }
        graphQL(data).then(res => {
            let responseData = res.data.data.registryItems.edges.map(item => { return { ...item.node } })
            this.props.dispatch(setRegistryProducts(responseData))

        }).catch(err => {
            console.error(err.message)
        })
    }

    getCategories = () => {
        let data = {
            query: GET_CATEGORIES,
            variables: {
                registryId: parseInt(ReducerDefaultData.myRegistry.registryId)
            },
        }
        graphQL(data).then(res => {
            let resData = res.data.data.categories.filter(category => category.subCategories.length > 0)
            this.props.dispatch(setCheckListItems(resData))

        }).catch(err => {
            console.error(err.message)
        })
    }

    moreOptionsData = [
        // TODO add option list
        {
            name: "View Item",
            action: () => alert("Clicked")
        },
    ]

    addProductHandler = (card) => {
        let existingProduct = this.props.stateAddedProducts.filter(product => product.item.sku === card.variants.edges[0].node.sku)
        if (existingProduct.length === 0) {
            const registryId = parseInt(ReducerDefaultData.myRegistry.registryId)
            const title = card.title
            const category = 2
            const tags = 1
            const storeItemUrl = card.onlineStoreUrl
            const sku = card.variants.edges[0].node.sku
            const price = parseFloat(card.variants.edges[0].node.price)
            const quantity = parseInt(card.quantity)
            const shopifyItemId = card.id
            const imageUrl = card.variants.edges[0].node.image.originalSrc

            let data = {
                query: ADD_REGISTRY_ITEM,
                variables: {
                    registryId: registryId,
                    title: title,
                    category: category,
                    tags: [tags],
                    storeItemUrl: storeItemUrl,
                    sku: sku,
                    price: price,
                    quantity: quantity,
                    shopifyItemId: shopifyItemId,
                    imageUrl: imageUrl,
                }
            }
            graphQL(data).then(res => {
                this.getProducts()
                this.getCategories()
            }).catch(err => {
                console.error(err.message)
            })
        }
        else{
            let data = {
                query: UPDATE_REGISTRY_ITEM,
                variables: {
                    registryItemId: existingProduct[0].item.id,
                    quantity: parseInt(card.quantity) + parseInt(existingProduct[0].quantity),
                }
            }
            graphQL(data).then(res => {
                this.getProducts()
                this.getCategories()
            }).catch(err => {
                console.error(err.message)
            })
        } 
    }

    render() {
        return (
            <div className="recommended-cart mt-6 p-4">
                <div className="row">
                    <div className="col title"><h2>{this.props.title} <span className="text-sm sub-title">Recommended </span></h2></div>
                    <div className="col ml-auto">
                        <MoreOption data={this.moreOptionsData} />
                        <button className="add-button text-body font-weight-bold py-1 px-3 float-right">Add on my own</button>
                    </div>
                </div>
                <div className="row pt-6">
                    {this.props.products.map((card, index) =>
                        <div className="col-4 cart-view">
                            <CardItem
                                className="mx-4"
                                data={card}
                                key={"CardItem-" + index}
                                incrementQuantityHandler={() => this.props.dispatch(incrementQuantity(card.id, 1))}
                                decrementQuantityHandler={() => this.props.dispatch(decrementQuantity(card.id, 1))}
                                addProductHandler={() => this.addProductHandler(card)}
                            />
                        </div>
                    )}
                </div>
                <div className="mt-6 card-buttom">
                    {/* TODO: Change text to dynamic */}
                    <div className="py-3 pl-4">How to pick up a perfect crib mattress?
                        <span className="ml-1 card-buttom-subtext">by Joanna Lee, March 13, 2020</span>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        stateAddedProducts: state.myRegistry.addedProducts,
        stateActiveRecommandation: state.myRegistry.activeRecommandation,
    }
}

export default connect(mapStateToProps)(ViewRecommend)