import React, { Component } from 'react'
import { connect } from "react-redux";
import AddedCard from "modules/commons/components/added-card/AddedCard";
import { likeAddedProduct, addRecommendedProduct, removeProductFromCart, setFilteredProduct, setFilterCategory, setCheckListItems, setRegistryProducts } from "../../MyRegistryActions";
import CheckList from "modules/my-registry/components/checkList/CheckList";
import CheckListItems from "modules/my-registry/components/checkList/CheckListItems";
import ViewRecommend from 'modules/commons/components/view-recommend/ViewRecommend';
import FilterSection from '../../components/filter-section/FilterSection';
import { graphQL, getRecommendation } from 'api/axios';
import { GET_PRODUCTS, GET_CATEGORIES, GET_RECOMMENDATIONS, REMOVE_REGISTRY_ITEM } from 'api/queries';
import ReducerDefaultData from 'data/ReducerDefaultData.json';

class Everything extends Component {

  cardData = this.props.stateProducts;
  addedCardData = this.props.stateAddedProducts;

  removeProductFromCartHandler = (card) => {
    let data = {
      query: REMOVE_REGISTRY_ITEM,
      variables: {
        registryItemId: card.item.id
      },
    }
    graphQL(data).then(res => {
      if(res.data.error){
        return
      }
      else{
        this.props.dispatch(removeProductFromCart(card.item.id))
        this.getCategories()
      }

    }).catch(err => {
      console.error(err.message)
    })
  }

  componentDidMount() {
    this.getCategories();
    this.getProducts();
    this.getRecommendationProducts();
  }

  getProducts = () => {
    let data = {
      query: GET_PRODUCTS,
      variables: {
        registryId: parseInt(ReducerDefaultData.myRegistry.registryId)
      }
    }
    graphQL(data).then(res => {
      let responseData = res.data.data.registryItems.edges.map(item => { return { ...item.node } })
      this.props.dispatch(setRegistryProducts(responseData))

    }).catch(err => {
      console.error(err.message)
    })
  }

  getCategories = () => {
    console.log("--->",ReducerDefaultData.myRegistry.registryId)
    let data = {
      query: GET_CATEGORIES,
      variables: {
        registryId: parseInt(ReducerDefaultData.myRegistry.registryId)
      },
    }
    graphQL(data).then(res => {
      let resData = res.data.data.categories.filter(category => category.subCategories.length > 0)
    console.log(res)
      this.props.dispatch(setCheckListItems(resData))

    }).catch(err => {
      console.error(err.message)
    })
  }

  getRecommendationProducts = () => {
    const recommededProductCount = 3
    const recommededProductType = "Jackets"
    let data = {
      query: GET_RECOMMENDATIONS,
      variables: {
        productCount: recommededProductCount,
        productType: "product_type:" + recommededProductType
      }
    }
    getRecommendation({ params: data }).then(res => {
      // let responseData = res.data.data.registryItems.edges.map(item => {return {...item.node}})
      // this.props.dispatch(setRegistryProducts(responseData))
      data = res.data.data.products.edges.map(item => {
        let resData = { ...item.node }
        resData['quantity'] = 1
        return resData
      })
      this.props.dispatch(addRecommendedProduct(data))
    }).catch(err => {
      console.error(err.message)
    })
  }

  // Updating product count in filter categories
  updateFilterList = (addedProducts) => {
    const newFilterList = [
      {
        id: "giftsAdded",
        display: "Gifts added",
        count: addedProducts.length,
        child: [
          {
            id: "mustHave",
            display: "Must have",
            count: addedProducts.filter(product => product.mostWanted).length
          },
          {
            id: "purchased",
            display: "Purchased",
            count: addedProducts.filter(product => product.isPurchased).length
          },
          {
            id: "reserved",
            display: "Reserved",
            count: addedProducts.filter(product => product.isReserved).length
          },
          {
            id: "available",
            display: "Available",
            count: 0
          }
        ]
      },
      {
        id: "giftsToAdd",
        display: "Gifts to add",
        count: 0
      }
    ]

    this.props.dispatch(setFilterCategory(newFilterList))
  }

  getFilteredProducts = (addedProducts, filterApplied) => {
    let filteredProductsId = []
    let filteredProducts = []

    if (filterApplied.parentFilter) {
      if (filterApplied.parentFilter === "giftsAdded") {
        if (filterApplied.childFilter.length > 0) {
          const childFilterArray = filterApplied.childFilter

          for (let product of addedProducts) {
            if (
              (childFilterArray.includes("mustHave") && product.mostWanted) ||
              (childFilterArray.includes("purchased") && product.isPurchased) ||
              (childFilterArray.includes("reserved") && product.isReserved)
            ) {
              filteredProductsId.push(product.item.id)
            }
          }
        } else {
          filteredProductsId = addedProducts.map(product => product.item.id)
        }
      }
    } else {
      filteredProductsId = addedProducts.map(product => product.item.id)
    }

    filteredProducts = filteredProductsId.map(itemId => {
      return addedProducts.find(product => product.item.id === itemId)
    })

    this.props.dispatch(setFilteredProduct(filteredProductsId))

    return filteredProducts
  }

  render() {
    this.updateFilterList(this.props.stateAddedProducts)
    let filteredProducts = this.getFilteredProducts(this.props.stateAddedProducts, this.props.stateFilterApplied)
    return (
      <>
        <div className="row">
          {/* View CheckList */}
          <div className="col-3">
            <CheckList />
            {this.props.stateCheckList.map(item => <CheckListItems key={item.categoryName} item={item} dispatch={this.props.dispatch} />)}
          </div>
          <div className="col-9">

            {/* Top Filter Section */}
            <FilterSection />

            {/* View Recommend if there is */}
            {/* TODO: View recommention by Categories */}
            {/* {this.props.stateActiveRecommandation && this.props.stateRecommendedProducts.find(product => product.category === this.props.stateActiveRecommandation) ? */}
            {/* <ViewRecommend title={this.props.stateActiveRecommandation} products={this.props.stateRecommendedProducts.filter((product, _) => product.category === this.props.stateActiveRecommandation)} /> */}
            {/* : null} */}
            <ViewRecommend title={this.props.stateActiveRecommandation} products={this.props.stateRecommendedProducts} />

            {/* Added Products */}
            {filteredProducts.map((card, index) =>
              <AddedCard
                data={card} key={"addedCard" + index}
                toggleFavorite={() => this.props.dispatch(likeAddedProduct(card.id))}
                removeProductFromCartHandler={() => this.removeProductFromCartHandler(card)}
              />
            )}
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    stateProducts: state.myRegistry.products,
    stateAddedProducts: state.myRegistry.addedProducts,
    stateFilterApplied: state.myRegistry.filterApplied,
    stateActiveRecommandation: state.myRegistry.activeRecommandation,
    stateRecommendedProducts: state.myRegistry.recommendedProducts,
    stateCheckList: state.myRegistry.checkList
  }
}


export default connect(
  mapStateToProps
)(Everything);
